:root {
	--nc-red: #e74514;
	--text: rgb(68, 68, 68);
	--shadow: rgb(189, 189, 189);
	--light-accent: #f3f3f3;
	--dark-accent: #ebe7e7;
}

@font-face {
	font-family: "Mark Pro Regular";
	src: url("/src/fonts/MARKPRO.OTF");
}
body {
	margin: 0;
}
.App {
	display: flex;
	flex-direction: column;
	text-align: center;
	font-family: "Mark Pro Regular";
	font-weight: bold;
	align-content: center;
	justify-content: center;
}

.Header {
	color: var(--nc-red);
	font-size: 2.7rem;
	padding: 0.75rem;
	padding-top: 1rem;
}

.ArticleCard {
	background-color: white;
	margin-bottom: 2rem;
	color: var(--text);
	box-shadow: 0 0 10px var(--dark-accent);
}

.ArticleCard h4 {
	background-color: var(--dark-accent);
	color: var(--text);
	width: 100%;
	padding: 0.65rem;
	margin: 0;
	width: inherit;
	box-shadow: ;
}

.ArticleCardBody {
	background-color: var(--light-accent);
	padding: 0.5rem 0rem 1rem;
	font-weight: lighter;
}

.ArticleCard h4 a {
	color: var(--light-text);
}

.ArticleCard h4 a:visited {
	color: var(--light-accent);
}

.ArticleCard h4:hover {
	background-color: #e22b13c7;
	color: #ffffff;
}

.ArticleCard a:visited {
	color: rgb(83, 83, 83);
}

.ArticleCard p {
	margin: 0;
	padding: 0.5rem;
	padding-top: 0;
	font-weight: lighter;
}

.FullArticle {
	display: flex;
	flex-direction: column;
	align-content: center;
	font-weight: lighter;
}
.FullArticle h2 {
	font-size: 1.25rem;
	font-weight: lighter;
	background-color: var(--dark-accent);
	padding: 1rem 0rem 1rem;
	margin: 0rem 1rem 0rem;
	box-shadow: 0 0 10px var(--dark-accent);
}
.FullArticle h4 {
	background-color: var(--light-accent);
	font-weight: bold;
	font-size: 0.9rem;
	padding: 0.75rem 2rem 0.75rem;
	margin: 0rem 1rem 0rem;
	box-shadow: 0 0 10px var(--dark-accent);
}
.FullArticle #body {
	font-weight: lighter;
	text-align: left;
	font-size: 0.9rem;
	/* overflow: hidden;
	text-overflow: scroll;
	display: -webkit-box;
	-webkit-line-clamp: 5;
	-webkit-box-orient: vertical; */
}

.FullArticle #text-wrapper {
	border: 2px var(--light-accent) solid;
	padding: 0.5rem 1rem 0.5rem;
	margin: 0rem 1rem 0rem;
}

.TopicSelector {
	padding-bottom: 0.25rem;
	width: 100%;
	background-color: white;
}

.TopicSelector button {
	background-color: var(--light-accent);
	border: none;
	font-family: inherit;
	font-size: inherit;
	padding: 0.4rem 1.5rem 0.4rem;
	margin: 0.5rem;
	margin-top: 0.75rem;
	box-shadow: 0 0 3px var(--dark-accent);
}

.TopicSelector button a {
	color: var(--text);
}

.Voter {
	display: flex;
	justify-content: center;
	align-items: center;
	align-content: center;
	background-color: var(--light-accent);
	padding: 1.5rem 0rem 2rem;
	color: var(--text);
	font-weight: lighter;
	margin: 3rem 0rem 3rem;
}

.Voter button {
	border: 2px none var(--dark-accent);
	margin-left: 1rem;
	margin-right: 1rem;
	padding: 0.15rem 0.75rem 0.15rem;
	color: var(--text);
	background-color: var(--dark-accent);
	box-shadow: 0 0 3px var(--shadow);
	border-radius: 100px;
}

.Voter button:active {
	box-shadow: 0 4px (--dark-accent);
	transform: translateY(1px);
}

.Voter button:hover {
	background-color: #e0d6d6a9;
}
#home .Voter {
	margin: 0rem;
	padding: 0rem 0rem 1rem;
}

#article-page .Voter {
	margin: 0rem 0rem 1rem;
	background-color: var(--dark-accent);
	padding: 1rem;
	margin: 0rem 1rem 1rem;
	border: none;
	box-shadow: 0 0 10px var(--dark-accent);
	font-size: 0.9rem;
}
#article-page .Voter button {
	padding: 0.1rem 0.6rem 0.1rem;
	background-color: var(--light-accent);
}

.SortBy {
	display: flex;
	justify-content: space-evenly;
	color: var(--light-text);
	background-color: white;
	font-size: 0.95rem;
	padding: 0.5rem 0rem 1rem;
}

.SortBy button {
	background-color: white;
	padding: 0rem;
	margin: 0rem;
	border: none;
	color: var(--text);
	font-size: inherit;
	font-family: inherit;
}

.CommentPoster {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.CommentPoster > * {
	margin: 0.5rem;
}
.CommentPoster textarea {
	width: 80%;
	height: 5rem;
}

.CommentCard {
	margin: 1rem 1rem 1rem;
	background-color: var(--light-accent);
}
.CommentCard p {
	padding: 1rem 0.5rem 0rem;
	font-weight: lighter;
	font-size: 0.9rem;
}
.CommentCard .Voter {
	background-color: var(--dark-accent);
	margin: 0rem;
	padding: 0.5rem 0rem 0.5rem;
	font-size: 0.8rem;
}
.CommentCard .Voter button {
	background-color: var(--light-accent);
}

.CommentCard #yourComment {
	background-color: var(--light-accent);
	padding-bottom: 0.75rem;
}

.CommentCard #yourComment button {
	border: 0;
	background-color: white;
}

.UserProfile {
	display: flex;
	flex-direction: column;
	margin: 1rem;
}
.UserProfile #bio {
	flex-wrap: nowrap;
	flex-direction: row;
}

#article-wrapper {
	background-color: var(--light-accent);
	padding: 0.5rem;
	margin: 0rem 0.5rem 1rem;
	width: 90%;
}

#user-article-link {
	background-color: var(--light-accent);
	font-weight: 900;
	overflow: hidden;
	text-overflow: scroll;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
}
#user-article-topic {
	font-weight: 10;
}
.PostArticle {
	display: flex;
	flex: 1;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	align-content: center;
	color: var(--text);
	font-family: "Mark Pro Regular";
}
.PostArticle > * {
	align-self: center;
	padding: 0.5rem;
	width: 80%;
	margin: 0.5rem;
	font-family: "Mark Pro Regular";
	border: 0;
	background-color: var(--light-accent);
}
.PostArticle #body {
	background-color: var(--light-accent);
}
.PostArticle #select {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: center;
	align-items: center;
	align-self: center;
	gap: 1rem;
	background-color: white;
}
.PostArticle #select > * {
	background-color: none;
	margin: 1rem;
}
.PostArticle #dropdown {
	font-family: "Mark Pro Regular";
	border: none;
	font-size: 1rem;
	padding: 0.25rem;
	border-radius: 2px;
}
.PostArticle button {
	background-color: var(--light-accent);
	/* border: 2px solid var(--shadow);
	border-radius: 0px; */
	border: 0;
}

ul {
	padding-inline-start: 0;
}

a {
	text-decoration: none;
	color: rgb(68, 68, 68);
}
img {
	width: 20%;
	height: 20%;
}

